import React, { useState, useEffect } from 'react'
import { Form, Row, Col, Button, Select, Card, Divider, Space, Modal, Input, message, Tooltip } from 'antd';
import Api from '../../helpers/api';
import moment from 'moment';

import {
    ReloadOutlined,
    ArrowLeftOutlined,
    SaveOutlined, PlusOutlined,
    DeleteOutlined,
    BarsOutlined,
    QrcodeOutlined,
    CloseCircleOutlined,
    CheckOutlined
} from '@ant-design/icons';
import handleApiError from '../../helpers/handleApiError';
import StockModal from '../../components/Modal/StockModal';
import TableMaterial from '../StockOpname/TableMaterial';
import NoteModal from '../../components/Modal/NoteModal';
import ScanModal from '../../components/Modal/ScanModal';
import hasAuthObject from '../../helpers/hasAuthObject';


// setup auth object config for this page
const authobjectCfg = {
    approve: 'stock-opname-approve', //button approve
    reject: 'stock-opname-reject', //button reject
}

const api = new Api();
const { Option } = Select;

const FormPage = (props) => {

    const [errors, setErrors] = useState({})
    const [loading, setLoading] = useState(false)
    const [form] = Form.useForm();

    const [select, setSelect] = useState({
        plant: [],
        room: [],
    })
    const [formData, setFormData] = useState({
        room_id: '',
        note: '',
        status: null,
        remark: ''
    })
    const [fetchRoomParameters, setFetchRoomParameters] = useState({
        plant_id: [],
        q: '',
    })
    const [showModal, setShowModal] = useState({
        modalStock: false,
        modalNote: false,
        modalScan: false,
        modalRejectNote: false,
    })

    const [selectedMaterial, setSelectedMaterial] = useState([])
    const [actualStockMaterial, setActualStockMaterial] = useState([])
    const [actualSerialMaterial, setActualSerialMaterial] = useState([])
    const [selectedMaterialDelete, setSelectedMaterialDelete] = useState([])
    const [activeButtonMaterialDelete, setActiveButtonMaterialDelete] = useState(true)

    useEffect(() => {
        const { add, view, edit } = props

        if (view || edit) {
            getEditData()
        }

        if (add) {
            form.setFieldsValue({
                code: `SO/${moment(new Date()).format("YYMMDD/HHmmss")}`,
            });
        }

        fetchPlant()
    }, []);

    useEffect(() => {
        if (select.plant.length > 0) {
            fetchRoom()
        }
    }, [fetchRoomParameters]);

    const handleInput = (value, name) => {
        setFormData({ ...formData, [name]: value })
    }

    const onFinish = (data) => {

        const { add, edit } = props

        if (selectedMaterial.length == 0) {
            setErrors({ material: 'The material field is required' })
            return
        }

        setLoading(true)

        if (add) {
            selectedMaterial.map((item, index) => {
                let indexStock = actualStockMaterial.map(stock => stock.id).lastIndexOf(item.id)
                selectedMaterial[index]['actual_stock'] = actualStockMaterial[indexStock] && actualStockMaterial[indexStock].id == item.id
                    ? actualStockMaterial[indexStock].val
                    : 0
            })
            data['stocks'] = selectedMaterial
        } else if (edit) {
            selectedMaterial.map((item, index) => {
                let indexStock = actualStockMaterial.map(stock => stock.id).lastIndexOf(item.stock.id)
                selectedMaterial[index]['stock']['actual_stock'] = actualStockMaterial[indexStock] && actualStockMaterial[indexStock].id == item.stock.id
                    ? actualStockMaterial[indexStock].val
                    : 0
            })
            data['stocks'] = selectedMaterial.map(item => item.stock)
        }

        let apiPost
        //handling if form add

        data['room_id'] = formData.room_id
        data['note'] = formData.note

        if (add) {
            apiPost = api.storeStockOpname(data)
        } else if (edit) {
            //handling if form edit
            const { id } = props.match.params

            data['serials'] = actualSerialMaterial

            apiPost = api.updateStockOpname(id, data)
        }

        apiPost.then(response => {
            setLoading(false)
            // console.log(response.data);
            message.success('save data success');
            props.history.goBack();
        }).catch(error => {
            setLoading(false)
            if (error.response && error.response.data) {
                const { errors } = error.response.data
                setErrors(errors)

                if (errors && errors.material) {
                    message.error(errors.material);
                }
            }

            handleApiError(error, props)
        });
    };

    const getEditData = () => {
        const { id } = props.match.params
        const { edit } = props

        api.viewStockOpname(id)
            .then(response => {
                const { code, room, room_id, note, status, details } = response.data

                if (edit && (status == 3 || status == 2)) { //rejected
                    props.history.push('/stock-opname/list');
                    return
                }

                form.setFieldsValue({
                    code,
                    plant_id: room ? room.plant.id : null,
                    room_id: room ? room.name : null,
                });
                setFormData({ room_id, note, status })
                setSelectedMaterial(details)
                setErrors({})
            }).catch(error => {
                handleApiError(error, props)
            })
    }

    const handleReset = () => {
        if (!inputDisabled) {
            form.setFieldsValue({
                plant_id: '',
                room_id: '',
                note: '',
            });
        }
        reset()
    }

    const fetchPlant = (q) => {
        api.getPlant({ q: q }).then(response => {
            const { data } = response.data;

            const newSelect = { ...select };
            newSelect.plant = data;
            setSelect(newSelect)

        }).catch(error => {
            handleApiError(error, props)
        })
    };

    const handleSearchPlant = (q) => {
        fetchPlant(q)
    }

    const handleChangePlant = (plantId) => {
        let parameters = { ...fetchRoomParameters }
        parameters['plant_id'] = [plantId]
        setFetchRoomParameters(parameters)
        fetchPlant('')
        form.setFieldsValue({
            room_id: '',
        });
        reset()
    }

    const reset = () => {
        setSelectedMaterialDelete(selectedMaterial.map(item => item.id))
        setSelectedMaterial([])
        setActualStockMaterial([])
        setActiveButtonMaterialDelete(true)
    }

    const fetchRoom = () => {
        api.getRoom({ ...fetchRoomParameters }).then(response => {
            const { data } = response.data;

            const newSelect = { ...select };
            newSelect.room = data;
            setSelect(newSelect)

        }).catch(error => {
            handleApiError(error, props)
        })
    };

    const handleSearchRoom = (q) => {
        let parameters = { ...fetchRoomParameters }
        parameters['q'] = q
        setFetchRoomParameters(parameters)
    }

    const handleChangeRoom = (value) => {
        let parameters = { ...fetchRoomParameters }
        parameters['q'] = ''
        setFetchRoomParameters(parameters)
        handleInput(value, 'room_id')
        reset()
    }

    const materialSelected = (value) => {
        setSelectedMaterial(value)
    }

    const handleChangeActualStockMaterial = (value) => {
        setActualStockMaterial(value)
    }

    const handleChangeActualSerialMaterial = (value) => {
        setActualSerialMaterial(value)
    }

    const handleSelectedMaterialDelete = (value) => {
        setSelectedMaterialDelete(value)
        setActiveButtonMaterialDelete(value.length > 0 ? false : true)
    }

    const handleDeleteSelectedMaterial = () => {
        let newSelectedMaterial = [...selectedMaterial]
        newSelectedMaterial = newSelectedMaterial.filter(val => !selectedMaterialDelete.includes(val.id));
        setSelectedMaterial(newSelectedMaterial)
        setActiveButtonMaterialDelete(true)
    }

    const handleShowModal = (type, value) => {
        const newShowModal = { ...showModal };
        newShowModal[type] = value;
        setShowModal(newShowModal)
    }

    const handleApprove = () => {
        Modal.confirm({
            title: `Are you sure approve this data?`,
            okText: 'Yes',
            okType: 'primary',
            cancelText: 'No',
            onOk: () => {
                handleApproveAction()
            },
            onCancel: () => {
                console.log('Cancel');
            },
        });
    }

    const handleApproveAction = () => {
        const { id } = props.match.params
        api.approveStockOpname(id)
            .then(response => {
                props.history.push('/stock-opname/list');
                message.success('success approved');
            }).catch(error => {
                if (error.response && error.response.data) {
                    const { errors } = error.response.data
                    setErrors(errors)

                    if (errors && errors.material) {
                        message.error(errors.material);
                    }
                }

                handleApiError(error, props)
            })
    }

    const handleReject = () => {
        Modal.confirm({
            title: `Are you sure reject this data?`,
            okText: 'Yes',
            okType: 'danger',
            cancelText: 'No',
            onOk: () => {
                handleShowModal('modalRejectNote', true)
            },
            onCancel: () => {
                console.log('Cancel');
            },
        });
    }

    const handleRejectAction = (isReject) => {
        if (isReject) {
            const { id } = props.match.params
            const { remark } = formData
            api.rejectStockOpname(id, { remark })
                .then(response => {
                    props.history.push('/stock-opname/list');
                    message.success('success rejected');
                }).catch(error => {
                    if (error.response && error.response.data) {
                        const { errors } = error.response.data
                        setErrors(errors)

                        if (errors && errors.material) {
                            message.error(errors.material);
                        }
                    }

                    handleApiError(error, props)
                })
        }
    }

    const handleResultScan = (value) => {
        const { id } = props.match.params
        api.scanStockOpname(id, { code: value })
            .then(response => {
                const { data } = response;
                getEditData()
                message.success(`success scan ${data.stock.material.material_code}`)
            }).catch(error => {
                if (error.response && error.response.data) {
                    const { errors } = error.response.data
                    setErrors(errors)

                    if (errors && errors.material) {
                        message.error(errors.material);
                    }
                }
                handleApiError(error, props)
            })
    }

    const { add, view, edit, history } = props
    const inputDisabled = view || edit

    return (
        <Card>
            <Form name="complex-form" onFinish={onFinish} labelCol={{ span: 4 }} wrapperCol={{ span: 8 }}
                form={form}>
                <Row>
                    <Col xs={24} sm={24} md={24} lg={18} xl={18}>

                        <Form.Item name="code" label="Code" labelAlign="left"
                            validateStatus={errors && errors.code ? 'error' : undefined}
                            help={errors ? errors.code : null}
                            rules={[{ required: true, message: 'Please input code' }]}>
                            <Input disabled={true} />
                        </Form.Item>

                        <Form.Item name="plant_id" label="Plant" labelAlign="left"
                            validateStatus={errors && errors.plant_id ? 'error' : undefined}
                            help={errors ? errors.plant_id : null}
                            rules={[{ required: !inputDisabled, message: 'Please input plant' }]}>
                            <Select
                                disabled={inputDisabled}
                                filterOption={false}
                                showSearch
                                onSearch={handleSearchPlant}
                                onChange={handleChangePlant}>

                                {select.plant.map(item => (
                                    <Option key={item.id} value={item.id}>{item.name}</Option>
                                ))}
                            </Select>
                        </Form.Item>

                        <Form.Item name="room_id" label="Room" labelAlign="left"
                            validateStatus={errors && errors.room_id ? 'error' : undefined}
                            help={errors ? errors.room_id : null}
                            rules={[{ required: !inputDisabled, message: 'Please input room' }]}>
                            <Select
                                disabled={inputDisabled}
                                filterOption={false}
                                showSearch
                                onSearch={handleSearchRoom}
                                onChange={handleChangeRoom}>

                                {select.room.map(item => (
                                    <Option key={item.id} value={item.id}>{item.name}</Option>
                                ))}
                            </Select>
                        </Form.Item>

                        <Form.Item label="Note" labelAlign="left"
                            validateStatus={errors && errors.note ? 'error' : undefined}
                            help={errors ? errors.note : null}
                            rules={[{ required: false, message: 'Please input note' }]}>
                            <Tooltip title="Note">
                                <Button icon={<BarsOutlined />} onClick={() => handleShowModal('modalNote', true)} />
                            </Tooltip>
                            <NoteModal
                                showNoteModal={showModal.modalNote}
                                handleShowModal={handleShowModal}
                                data={formData.note}
                                handleInput={handleInput}
                                modalName='modalNote'
                                formName='note'
                                updateAction={false}
                            />
                        </Form.Item>

                    </Col>
                    <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                        <Space direction="vertical" size="large" style={{ width: '100%' }}>
                            <Row>
                                <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                                    <Space>
                                        {view || edit ? null : (
                                            <>
                                                <Tooltip title='Add material'>
                                                    <Button type="primary"
                                                        onClick={() => handleShowModal('modalStock', true)}
                                                        icon={<PlusOutlined />}
                                                    />
                                                </Tooltip>
                                                <Tooltip title='Delete material'>
                                                    <Button
                                                        danger
                                                        disabled={activeButtonMaterialDelete}
                                                        onClick={handleDeleteSelectedMaterial}
                                                        icon={<DeleteOutlined />}
                                                    />
                                                </Tooltip>
                                            </>
                                        )}
                                        {formData.status == 0 && edit ? (
                                            // 0 = draft
                                            <>
                                                <Tooltip title='Scan material'>
                                                    <Button
                                                        onClick={() => handleShowModal('modalScan', true)}
                                                        icon={<QrcodeOutlined />}
                                                    />
                                                </Tooltip>
                                                <ScanModal
                                                    showScanModal={showModal.modalScan}
                                                    handleShowModal={handleShowModal}
                                                    resultScan={handleResultScan}
                                                />
                                            </>
                                        ) : null}
                                        {formData.status == 1 && view ? (
                                            // 1 = waiting approve
                                            <>
                                                {hasAuthObject(authobjectCfg.approve) && (
                                                    <Tooltip title='Approve'>
                                                        <Button
                                                            type='primary'
                                                            onClick={handleApprove}
                                                            icon={<CheckOutlined />}
                                                        />
                                                    </Tooltip>
                                                )}

                                                {hasAuthObject(authobjectCfg.reject) && (
                                                    <>
                                                        <Tooltip title='Reject'>
                                                            <Button
                                                                danger
                                                                onClick={handleReject}
                                                                icon={<CloseCircleOutlined />}
                                                            />
                                                        </Tooltip>
                                                        <NoteModal
                                                            showNoteModal={showModal.modalRejectNote}
                                                            handleShowModal={handleShowModal}
                                                            data={formData.remark}
                                                            handleInput={handleInput}
                                                            modalName='modalRejectNote'
                                                            formName='remark'
                                                            updateAction={handleRejectAction}
                                                        />
                                                    </>
                                                )}
                                            </>
                                        ) : null}
                                        {errors && errors.material ?
                                            <div className="ant-form-item-explain ant-form-item-explain-error">
                                                <div role="alert">{errors.material}</div>
                                            </div>
                                            : null
                                        }
                                    </Space>
                                </Col>
                            </Row>
                            <Row>
                                <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                                    <TableMaterial
                                        {...props}
                                        selectedMaterial={selectedMaterial}
                                        actualStockMaterial={handleChangeActualStockMaterial}
                                        actualSerialMaterial={handleChangeActualSerialMaterial}
                                        selectedMaterialDelete={handleSelectedMaterialDelete} />
                                </Col>
                            </Row>
                        </Space>
                    </Col>
                </Row>

                <br />
                <p><span style={{ color: '#f5222d', fontSize: 14 }}>*</span> Required</p>
                <Divider />

                <Space>
                    <Button onClick={() => history.goBack()}
                        icon={<ArrowLeftOutlined />}
                    >Back</Button>

                    {view || edit ? null : (
                        <Button onClick={() => handleReset()}
                            icon={<ReloadOutlined />}
                        >Reset</Button>
                    )}

                    {view ?? (
                        <Button type="primary" htmlType="submit" loading={loading}
                            icon={<SaveOutlined />}
                        >Save</Button>
                    )}
                </Space>
            </Form>

            <Modal
                title="Select Stock"
                open={showModal.modalStock}
                onCancel={() => handleShowModal('modalStock', false)}
                onOk={() => handleShowModal('modalStock', false)}
                cancelButtonProps={{ style: { display: 'none' } }}
                width={1000}>
                <StockModal
                    materialSelected={materialSelected}
                    selectedMaterialDelete={selectedMaterialDelete}
                    roomId={formData.room_id}
                />
            </Modal>
        </Card >
    )
}

export default FormPage;