import React, { useState } from 'react'
import { Modal, message, Space, Button } from 'antd';
//helpers
import QrReader from 'modern-react-qr-reader'

const ScanModal = (props) => {

    const [activeScanner, setActiveScanner] = useState(false);

    const { showScanModal, handleShowModal, resultScan } = props

    const handleShow = () => {
        handleShowModal('modalScan', !showScanModal)
    }

    const handleScan = data => {
        if (data) {
            setActiveScanner(false)
            resultScan(data)
            handleShow()
        }
    }

    const handleError = err => {
        console.error(err)
        message.error(err)
    }

    const handleClickScan = (condition) => {
        setActiveScanner(condition)
    }

    return (
        <Modal
            title="Scan Material"
            open={showScanModal}
            onCancel={handleShow}
            onOk={handleShow}
            footer={null}
        >
            {activeScanner && (
                <QrReader
                    facingMode={"environment"}
                    onError={handleError}
                    onScan={handleScan}
                    style={{ width: '100%', marginBottom: 20 }}
                />
            )}

            <Space>
                <Button
                    disabled={activeScanner}
                    type='primary'
                    onClick={() => handleClickScan(true)}
                >Start Scan
                </Button>

                <Button
                    disabled={!activeScanner}
                    onClick={() => handleClickScan(false)}
                >Cancel Scan
                </Button>
            </Space>
        </Modal>
    );
}

export default ScanModal;